<script setup>
import { ref, computed, onMounted } from 'vue'
import { onLoad } from '@dcloudio/uni-app'

// È°µÈù¢Áä∂ÊÄÅ
const statusBarHeight = ref(0)

// Ê∂àÊÅØÊï∞ÊçÆ
const message = ref({
  id: '1',
  sender_name: 'Á≥ªÁªüÁÆ°ÁêÜÂëò',
  title: 'ÈáçË¶ÅÁ≥ªÁªüÁª¥Êä§ÈÄöÁü•',
  content: `Â∞äÊï¨ÁöÑÁî®Êà∑Ôºö

‰∏∫‰∫ÜÊèêÂçáÁ≥ªÁªüÊÄßËÉΩÂíåÁî®Êà∑‰ΩìÈ™åÔºåÊàë‰ª¨Â∞Ü‰∫éÊú¨Âë®Êú´ËøõË°åÈáçË¶ÅÁöÑÁ≥ªÁªüÁª¥Êä§ÂçáÁ∫ß„ÄÇÂÖ∑‰ΩìÂÆâÊéíÂ¶Ç‰∏ãÔºö

üïê Áª¥Êä§Êó∂Èó¥Ôºö2025Âπ¥8Êúà21Êó• 02:00 - 06:00
üîß ÂΩ±ÂìçËåÉÂõ¥ÔºöÂÖ®Âπ≥Âè∞ÊúçÂä°ÊöÇÂÅú
‚è∞ È¢ÑËÆ°ÊÅ¢Â§çÔºöÂΩìÊó•‰∏äÂçà6ÁÇπÂâçÂÆåÂÖ®ÊÅ¢Â§ç

Áª¥Êä§ÊúüÈó¥ÔºåÊÇ®ÂèØËÉΩÈÅáÂà∞‰ª•‰∏ãÊÉÖÂÜµÔºö
‚Ä¢ Êó†Ê≥ïÊ≠£Â∏∏ÁôªÂΩïÂíå‰ΩøÁî®Âπ≥Âè∞ÊúçÂä°
‚Ä¢ Êï∞ÊçÆÂêåÊ≠•ÂèØËÉΩÂá∫Áé∞Âª∂Ëøü  
‚Ä¢ ÈÉ®ÂàÜÂäüËÉΩ‰∏¥Êó∂‰∏çÂèØÁî®

[img:https://fuss10.elemecdn.com/3/28/bbf893f792f03a54408b3b7a7ebf0jpeg.jpeg]

Êú¨Ê¨°ÂçáÁ∫ßÂ∞ÜÂ∏¶Êù•‰ª•‰∏ãÊîπËøõÔºö
‚ú® ÂÖ®Êñ∞ÁöÑÁî®Êà∑ÁïåÈù¢ËÆæËÆ°
üöÄ Êõ¥Âø´ÁöÑÂìçÂ∫îÈÄüÂ∫¶ÔºàÊèêÂçá40%Ôºâ
üîí Â¢ûÂº∫ÁöÑÂÆâÂÖ®Èò≤Êä§Êú∫Âà∂
üì± ‰ºòÂåñÁßªÂä®Á´Ø‰ΩìÈ™å

[video:https://media.w3.org/2010/05/sintel/trailer.mp4]

‰∏∫‰∫ÜÁ°Æ‰øùÊÇ®ÁöÑÊï∞ÊçÆÂÆâÂÖ®ÔºåÂª∫ËÆÆÊÇ®Ôºö
1. ÊèêÂâç‰øùÂ≠òÈáçË¶ÅÂ∑•‰ΩúÂÜÖÂÆπ
2. ÈÅøÂÖçÂú®Áª¥Êä§ÊúüÈó¥ËøõË°åÈáçË¶ÅÊìç‰Ωú
3. ÂÖ≥Ê≥®Êàë‰ª¨ÁöÑÂÆòÊñπÂÖ¨ÂëäËé∑ÂèñÊúÄÊñ∞ËøõÂ±ï

ÊÑüË∞¢ÊÇ®ÁöÑÁêÜËß£‰∏éÊîØÊåÅÔºÅÂ¶ÇÊúâÁ¥ßÊÄ•ÈóÆÈ¢òÔºåËØ∑ËÅîÁ≥ªÂÆ¢ÊúçÁÉ≠Á∫øÔºö400-123-4567

Á•ùÊÇ®‰ΩøÁî®ÊÑâÂø´ÔºÅ`,
  type: 'maintenance',
  priority: 'high',
  created_at: '2025-08-19T14:30:00.000Z'
})

// Ëß£ÊûêÊ∂àÊÅØÂÜÖÂÆπ
const { textContent, mediaItems } = parseMessageContent(message.value.content || '')

// ÁîüÂëΩÂë®Êúü
onMounted(() => {
  const sysInfo = uni.getSystemInfoSync()
  statusBarHeight.value = sysInfo.statusBarHeight || 0
})

onLoad((options) => {
  // ËøôÈáåÂèØ‰ª•Ê†πÊçÆ‰º†ÂÖ•ÁöÑÂèÇÊï∞Âä†ËΩΩÂÖ∑‰ΩìÊ∂àÊÅØ
  if (options.id) {
    // loadMessageDetail(options.id)
  }
})

// Ëß£ÊûêÊ∂àÊÅØÂÜÖÂÆπÔºåÂàÜÁ¶ªÊñáÊú¨ÂíåÂ™í‰Ωì
function parseMessageContent(content) {
  if (!content) return { textContent: '', mediaItems: [] }
  
  const mediaItems = []
  let textContent = content
  
  // ÂåπÈÖçÂõæÁâá
  const imageRegex = /\[(?:img|image):([^\]]+)\]/g
  textContent = textContent.replace(imageRegex, (match, url) => {
    mediaItems.push({
      type: 'image',
      url: url.trim()
    })
    return ''
  })
  
  // ÂåπÈÖçËßÜÈ¢ë
  const videoRegex = /\[(?:video|vid):([^\]]+)(?:\|poster:([^\]]+))?\]/g
  textContent = textContent.replace(videoRegex, (match, url, poster) => {
    mediaItems.push({
      type: 'video',
      url: url.trim(),
      poster: poster ? poster.trim() : ''
    })
    return ''
  })
  
  // Ê∏ÖÁêÜÂ§ö‰ΩôÁöÑÁ©∫Ë°å
  textContent = textContent.replace(/\n\s*\n\s*\n/g, '\n\n').trim()
  
  return { textContent, mediaItems }
}

// Ê†ºÂºèÂåñËØ¶ÁªÜÊó∂Èó¥
const formatDetailTime = (timeStr) => {
  if (!timeStr) return ''
  
  const time = new Date(timeStr)
  const year = time.getFullYear()
  const month = String(time.getMonth() + 1).padStart(2, '0')
  const day = String(time.getDate()).padStart(2, '0')
  const hours = String(time.getHours()).padStart(2, '0')
  const minutes = String(time.getMinutes()).padStart(2, '0')
  
  return `${year}Âπ¥${month}Êúà${day}Êó• ${hours}:${minutes}`
}

// Ëé∑ÂèñÁ±ªÂûãÊ†áÁ≠æ
const getTypeLabel = (type) => {
  const labels = {
    'announcement': 'ÂÖ¨Âëä',
    'maintenance': 'Áª¥Êä§',
    'event': 'Ê¥ªÂä®',
    'security': 'ÂÆâÂÖ®',
    'update': 'Êõ¥Êñ∞',
    'normal': 'ÈÄöÁü•'
  }
  return labels[type] || 'ÈÄöÁü•'
}

// Ëé∑ÂèñÊâÄÊúâÂõæÁâáURL
const getAllImageUrls = () => {
  return mediaItems.filter(item => item.type === 'image').map(item => item.url)
}

// È¢ÑËßàÂõæÁâá
const previewImage = (current, urls) => {
  uni.previewImage({
    current,
    urls
  })
}

// ÂõæÁâáÂä†ËΩΩÊàêÂäü
const onImageLoad = (e) => {
  console.log('ÂõæÁâáÂä†ËΩΩÊàêÂäü', e)
}

// ÂõæÁâáÂä†ËΩΩÂ§±Ë¥•
const onImageError = (e) => {
  console.log('ÂõæÁâáÂä†ËΩΩÂ§±Ë¥•', e)
  uni.showToast({
    title: 'ÂõæÁâáÂä†ËΩΩÂ§±Ë¥•',
    icon: 'none',
    duration: 2000
  })
}

// ËøîÂõû‰∏ä‰∏ÄÈ°µ
const goBack = () => {
  uni.navigateBack()
}
</script>

